<template>
  <div class="tk-console">
    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">Administration</h6>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <base-button
            size="sm"
            type="white"
            class="text-main"
            @click.stop="showAddAccount = true"
            >Create</base-button
          >
          <base-button
            size="sm"
            type="white"
            class="text-main"
            @click.stop="showAccountType = true"
            >Account Type</base-button
          >
        </div>
      </div>
    </base-header>

    <div class="container-fluid mt--6">
      <div class="card-wrapper">
        <div class="card">
          <div class="card-body">
            <el-table
              class="table-responsive align-items-center table-flush"
              header-row-class-name="thead-light"
              :data="accounts"
            >
              <el-table-column label="Name" prop="name"></el-table-column>
              <el-table-column label="Email" prop="email"></el-table-column>
              <el-table-column
                label="Password"
                prop="password"
              ></el-table-column>
              <el-table-column label="Sign up date" prop="createTime">
                <template v-slot="{ row }">
                  <span>{{ formatMMDDYY(row.createTime) }}</span>
                </template>
              </el-table-column>
              <el-table-column label="Account type" prop="accountType">
                <template v-slot="{ row }">
                  <span>{{ formatAccountType(row.typeId) }}</span>
                </template>
              </el-table-column>
              <el-table-column label="Logs">
                <!-- <template v-slot="{ row }">
                  <span>{{ row.logs }}</span>
                </template> -->
                <span>0</span>
              </el-table-column>
              <el-table-column label="Permission">
                <template v-slot="{ row }">
                  <!-- <span>{{ row.permission }}</span> -->
                  <el-dropdown
                    trigger="click"
                    class="dropdown"
                    style="width: 100%"
                  >
                    <div>{{ getPermissionsByTypeId(row.typeId).length }}</div>
                    <el-dropdown-menu
                      class="dropdown-menu dropdown-menu-arrow show"
                      slot="dropdown"
                    >
                      <el-dropdown-item
                        v-for="item in accountType"
                        :key="item.id"
                      >
                        <div @click="changeAccountType(row, item)">
                          {{ item.name }} ({{
                            getPermissionsByTypeId(item.id).length
                          }})
                        </div>
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </template>
              </el-table-column>
              <el-table-column width="54">
                <template v-slot="{ row }">
                  <div class="tk-console-table-fa-btn">
                    <i
                      class="fas fa-minus-circle tk-font-color-lightGray tk-px-margin-right-px20"
                      @click.stop="removeAccount(row)"
                    ></i>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>

    <modal :show.sync="showAccountType" id="accountType">
      <template slot="header">
        <h5 class="modal-title">Account type</h5>
      </template>
      <div class="tk-popup-content">
        <div style="margin-bottom: 20px; text-align: center">
          <el-input
            v-model="newAccountType"
            placeholder="New account type"
            style="width: 300px"
          ></el-input>
          <span
            class="tk-font-color-main tk-px-margin-left-px20 tk-cursor-pointer"
            @click="addNewAccountType"
            >Add</span
          >
        </div>
        <el-table
          class="table-responsive align-items-center table-flush"
          header-row-class-name="thead-light"
          :data="accountType"
        >
          <el-table-column
            label="Account Type"
            prop="name"
            width="130"
          ></el-table-column>
          <el-table-column
            label="Shake to Report"
            prop="shakeToReport"
            width="88"
          >
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('5') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('5'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('5')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Screen Shots" prop="screenShots" width="72">
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('4') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('4'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('4')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Dynamic link" prop="dynamicLink" width="88">
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('3') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('3'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('3')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Console" prop="console" width="88">
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('2') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('2'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('2')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Logs" prop="logs" width="60">
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('1') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('1'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('1')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column
            label="Administration"
            prop="administration"
            width="140"
          >
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('0') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('0'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('0')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="Messaging" prop="messaging" min-width="100">
            <template v-slot="{ row }">
              <span>
                <i
                  class="fas fa-check-square tk-font-color-main"
                  v-if="row.permissions.indexOf('6') > -1"
                  @click.stop="
                    row.permissions.splice(row.permissions.indexOf('6'), 1)
                  "
                ></i>
                <i
                  class="far fa-square tk-font-color-lightGray"
                  v-else
                  @click.stop="row.permissions.push('6')"
                ></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column width="40">
            <template v-slot="{ row }">
              <div class="tk-console-table-fa-btn">
                <i
                  class="fas fa-minus-circle tk-font-color-lightGray tk-px-margin-right-px10"
                  @click="removeAccountType(row)"
                ></i>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <template slot="footer">
        <!-- <base-button
          type="secondary"
          @click.prevent="showAccountType = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        > -->
        <base-button
          type="primary"
          @click.prevent="showAccountType = false"
          class="tk-font-color-white tk-bg-color-main tk-border-none"
          >Done</base-button
        >
      </template>
    </modal>

    <modal :show.sync="showAddAccount" id="addAccount">
      <template slot="header">
        <h5 class="modal-title">New Account</h5>
      </template>
      <div class="tk-popup-content">
        <div style="margin-bottom: 20px; text-align: center">
          <el-input
            v-model="name"
            placeholder="Name"
            style="width: 300px"
          ></el-input>
          <br />
          <br />
          <el-input
            v-model="email"
            placeholder="Email"
            style="width: 300px"
          ></el-input>
          <br />
          <br />
          <el-input
            v-model="password"
            placeholder="Password"
            style="width: 300px"
          ></el-input>
          <br />
          <br />
          <el-select v-model="currentAccountType" style="width: 300px">
            <el-option
              v-for="item in accountType"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showAddAccount = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <!-- <base-button
          type="primary"
          @click.prevent="createNewAccount"
          class="tk-font-color-white tk-bg-color-main tk-border-none"
          >Create</base-button
        > -->
        <!-- style="margin: 24px auto 1rem" -->
        <!-- width="180" -->
        <button-with-loading
          text="Create"
          style="margin: 0 0 0 1rem"
          @start="createNewAccount"
          type="main"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
      </template>
    </modal>

    <modal :show.sync="showRemoveAccount" id="removeAccount">
      <template slot="header">
        <h5 class="modal-title">Delete Account</h5>
      </template>
      <div class="tk-popup-content">
        Are you sure to delete the account
        <strong v-if="deleteAccountIndex >= 0"
          >[{{ accounts[deleteAccountIndex].name }}]</strong
        >
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showRemoveAccount = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="primary"
          @click.prevent="confirmRemoveAccount"
          class="tk-font-color-white tk-bg-color-red tk-border-none"
          >Delete</base-button
        >
      </template>
    </modal>
  </div>
</template>

<script>
import $ from "jquery";
import query from "@/services/query";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
  Select,
  Option,
} from "element-ui";
export default {
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    [Select.name]: Select,
    [Option.name]: Option,
    buttonWithLoading: () =>
      import("@/tkViews/components/layout/ButtonWithLoading"),
  },
  data() {
    return {
      deleteAccountIndex: -1,
      showRemoveAccount: false,
      showAccountType: false,
      newAccountType: "",
      newAccountTypePermissions: [],
      showAddAccount: false,
      name: "",
      email: "",
      password: "",
      currentAccountType: "",
      newAccount: {
        userId: "",
        name: "",
        email: "",
        password: "",
        createTime: "",
        typeId: "",
      },

      accounts: [],
      accountType: [],
      permissions: [],

      consoleUser: null,
      isButtonLoadingComplete: false,
    };
  },
  async mounted() {
    let user = localStorage.getItem("auth_user");
    if (!this.$tools.isNull(user)) {
      user = JSON.parse(user);
    }
    let management = await query.getAccountManagement();

    this.accounts = management.accounts;
    this.accounts.some((item) => {
      if (item.userId == user.uid) {
        this.consoleUser = item;
        return;
      }
    });

    this.accountType = management.accountType;
    this.permissions = management.permissions;
    this.initHover();

    //add log
    if (!this.$tools.isNull(this.consoleUser)) {
      await query.createLogs({
        id: this.$tools.generateId(),
        email: this.consoleUser.email,
        name: this.consoleUser.name,
        createTime: String(parseInt(new Date().getTime() / 1000)),
        action: "Query",
        documentPath: "/config/accountManagement",
        parameters: "",
        pageName: "Administration",
      });
    }
  },
  watch: {
    accounts: {
      handler(newVal, oldVal) {
        this.initHover();
        query.updateAccountManagement({
          accounts: this.accounts,
        });
      },
      deep: true,
    },
    accountType: {
      handler(newVal, oldVal) {
        query.updateAccountManagement({
          accountType: this.accountType,
        });
      },
      deep: true,
    },
  },
  methods: {
    async createUser(type) {
      let user = {};

      switch (type) {
        case 0:
          user = {
            name: "Marian",
            email: "marian.lawas@yahoo.com",
            password: "Spelist100",
            typeId: "2",
          };
          break;
        case 1:
          user = {
            name: "tkMarketer",
            email: "tkmarketer@tunekey.test",
            password: "tunekey100",
            typeId: "2",
          };
          break;
        case 2:
          user = {
            name: "tunekeyTeam1",
            email: "tunekeyTeam1@gmail.com",
            password: "tunekey100",
            typeId: "0",
          };
          break;
      }

      let func = this.$firebase
        .functions()
        .httpsCallable("createManageAccount");
      func(user).then((result) => {
        // console.log(result);
      });
    },
    getPermissionsByTypeId(typeId) {
      let arr = [];
      this.accountType.some((item) => {
        if (item.id == typeId) {
          arr = item.permissions;
          return;
        }
      });
      return arr;
    },
    formatAccountType(typeId) {
      let str = "";
      this.accountType.some((item) => {
        if (item.id == typeId) {
          str = item.name;
          return;
        }
      });
      return str;
    },
    formatMMDDYY(time) {
      return this.$tools.formatMMDDYY(time * 1000);
    },
    async changeAccountType(row, item) {
      row.typeId = item.id;

      //add log
      await query.createLogs({
        id: this.$tools.generateId(),
        email: this.consoleUser.email,
        name: this.consoleUser.name,
        createTime: String(parseInt(new Date().getTime() / 1000)),
        action: "Update",
        documentPath: "/config/accountManagement",
        parameters: JSON.stringify(row),
        pageName: "Administration",
      });
    },
    initHover() {
      // console.log("init hover");
      setTimeout(() => {
        $(".el-table__row").unbind("mouseover");
        $(".el-table__row").unbind("mouseleave");
        for (let i = 0; i < $(".el-table__row").length; i++) {
          let index = i;
          $(".el-table__row")
            .eq(index)
            .mouseover(() => {
              $(".el-table__row")
                .eq(index)
                .find(".tk-console-table-fa-btn")
                .show();
            });
          $(".el-table__row")
            .eq(index)
            .mouseleave(() => {
              $(".el-table__row")
                .eq(index)
                .find(".tk-console-table-fa-btn")
                .hide();
            });
        }
      }, 100);
    },
    async createNewAccount() {
      let self = this;
      if (self.$tools.isNull(self.name)) {
        self.$notify({
          message: "Name is empty!",
          type: "error",
        });
        return false;
      }

      if (self.$tools.isNull(self.email)) {
        self.$notify({
          message: "Email is empty!",
          type: "error",
        });
        return false;
      }

      if (self.$tools.isNull(self.password)) {
        self.$notify({
          message: "Password is empty!",
          type: "error",
        });
        return false;
      }

      if (self.$tools.isNull(self.currentAccountType)) {
        self.$notify({
          message: "Account type is empty!",
          type: "error",
        });
        return false;
      }

      let exist = false;
      await self.accounts.some((item, i) => {
        if (item.email == self.email) {
          exist = true;
          return;
        }
      });

      let accountTypeIndex = 0;
      await self.accountType.some((item, i) => {
        if (item.typeName == self.currentAccountType) {
          accountTypeIndex = i;
          return;
        }
      });

      if (!exist) {
        this.isButtonLoadingComplete = true;
        let func = this.$firebase
          .functions()
          .httpsCallable("createManageAccount");
        await func({
          name: self.name,
          email: self.email,
          password: self.password,
          typeId: self.currentAccountType,
        }).then(async (result) => {
          // console.log(result);
          self.accounts.push(result.data.data);

          //add log
          query.createLogs({
            id: self.$tools.generateId(),
            email: self.consoleUser.email,
            name: self.consoleUser.name,
            createTime: String(parseInt(new Date().getTime() / 1000)),
            action: "Create",
            documentPath: "/config/accountManagement",
            parameters: JSON.stringify(result.data.data),
            pageName: "Administration",
          });

          setTimeout(() => {
            self.name = "";
            self.email = "";
            self.password = "";
            self.currentAccountType = "";
            self.showAddAccount = false;
            self.initHover();
          }, 100);
        });
        this.isButtonLoadingComplete = false;
      } else {
        self.$notify({
          message: "Duplicate email!",
          type: "warning",
        });
      }
    },
    removeAccount(row) {
      let index = 0;
      this.showRemoveAccount = true;
      this.accounts.some((item, i) => {
        if (item.userId == row.userId) {
          index = i;
          return;
        }
      });
      this.deleteAccountIndex = index;
    },
    async confirmRemoveAccount() {
      let deleteAccount = this.accounts[this.deleteAccountIndex];
      this.accounts.splice(this.deleteAccountIndex, 1);
      this.showRemoveAccount = false;
      this.deleteAccountIndex = -1;

      let func = this.$firebase
        .functions()
        .httpsCallable("deleteManageAccount");
      func({
        userId: deleteAccount.userId,
      }).then((result) => {
        // console.log(result);
        if (result.data.code == 200) {
          //add log
          query.createLogs({
            id: self.$tools.generateId(),
            email: self.consoleUser.email,
            name: self.consoleUser.name,
            createTime: String(parseInt(new Date().getTime() / 1000)),
            action: "Delete",
            documentPath: "/config/accountManagement",
            parameters: JSON.stringify(deleteAccount),
            pageName: "Administration",
          });
        }
      });
    },
    async removeAccountType(row) {
      let index = 0;
      this.accountType.some((item, i) => {
        if (item.id == row.id) {
          index = i;
          return;
        }
      });
      let deleteType = this.accountType[index];
      this.accountType.splice(index, 1);

      //add log
      await query.createLogs({
        id: this.$tools.generateId(),
        email: this.consoleUser.email,
        name: this.consoleUser.name,
        createTime: String(parseInt(new Date().getTime() / 1000)),
        action: "Delete",
        documentPath: "/config/accountManagement",
        parameters: JSON.stringify(deleteType),
        pageName: "Administration",
      });
    },
    async addNewAccountType() {
      let isExist = false;

      await this.accountType.some((item, i) => {
        if (item.typeName == this.newAccountType) {
          isExist = true;
          return;
        }
      });

      if (isExist) {
        this.$notify({
          message: "Duplicate account type!",
          type: "warning",
        });
      } else {
        if (this.newAccountType == "") {
          this.$notify({
            message: "Empty value!",
            type: "error",
          });
        } else {
          let newAccountType = {
            id: new Date().getTime(),
            name: this.newAccountType,
            permissions: this.newAccountTypePermissions,
          };
          this.accountType.push(newAccountType);

          query.updateAccountManagement({
            accountType: this.accountType,
          });

          //add log
          await query.createLogs({
            id: this.$tools.generateId(),
            email: this.consoleUser.email,
            name: this.consoleUser.name,
            createTime: String(parseInt(new Date().getTime() / 1000)),
            action: "Create",
            documentPath: "/config/accountManagement",
            parameters: JSON.stringify(newAccountType),
            pageName: "Administration",
          });

          self.initHover();
        }
      }

      this.newAccountType = "";
      this.newAccountTypePermissions = [];
    },
  },
};
</script>

<style>
.tk-console .cell i {
  font-size: 1.2rem !important;
}

#accountType .modal-dialog {
  max-width: 880px !important;
}

#accountType .modal-content {
  width: 880px !important;
}

#accountType th {
  padding: 0 10px !important;
}
#accountType td {
  padding: 10px !important;
}
#accountType .modal-body {
  padding-top: 0 !important;
}
#accountType .tk-popup-content {
  max-height: 64vh;
}
.tk-console-table-fa-btn {
  display: none;
}
#addAccount .el-input__inner:focus {
  border-color: #71d9c2 !important;
}
</style>